import React, { useState } from 'react';
import { Form, Button, InputGroup, Alert } from 'react-bootstrap';
import { useCart } from '../../context/CartContext.enhanced';
import { sanitize } from '../../security/sanitizer';
import XSSProtection from '../../security/xssProtection';

/**
 * Discount Code Input Component with Security
 */
export const DiscountCodeInput = ({ 
  onApply,
  validateCode // Optional: function to validate code with backend
}) => {
  const { discount, applyDiscount, removeDiscount } = useCart();
  const [code, setCode] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  
  /**
   * Validate discount code format
   */
  const validateCodeFormat = (input) => {
    // Sanitize input
    const cleaned = XSSProtection.stripHTML(input).toUpperCase();
    
    // Check length
    if (cleaned.length < 3 || cleaned.length > 20) {
      throw new Error('M√£ gi·∫£m gi√° ph·∫£i c√≥ 3-20 k√Ω t·ª±');
    }
    
    // Only allow alphanumeric and dash
    if (!/^[A-Z0-9-]+$/.test(cleaned)) {
      throw new Error('M√£ gi·∫£m gi√° ch·ªâ ch·ª©a ch·ªØ c√°i, s·ªë v√† d·∫•u g·∫°ch ngang');
    }
    
    // Prevent common XSS patterns
    const dangerousPatterns = [
      'SCRIPT',
      'JAVASCRIPT',
      'ONERROR',
      'ONLOAD',
      '<',
      '>',
      '&'
    ];
    
    if (dangerousPatterns.some(pattern => cleaned.includes(pattern))) {
      throw new Error('M√£ gi·∫£m gi√° kh√¥ng h·ª£p l·ªá');
    }
    
    return cleaned;
  };
  
  /**
   * Handle apply discount
   */
  const handleApply = async () => {
    try {
      setError('');
      setSuccess('');
      setLoading(true);
      
      // Validate format
      const validatedCode = validateCodeFormat(code);
      
      // If custom validation provided, use it
      if (validateCode) {
        const result = await validateCode(validatedCode);
        
        if (!result.valid) {
          throw new Error(result.message || 'M√£ gi·∫£m gi√° kh√¥ng h·ª£p l·ªá');
        }
        
        // Apply discount from backend
        applyDiscount(validatedCode, result.percentage);
        setSuccess(`ƒê√£ √°p d·ª•ng m√£ gi·∫£m gi√° ${result.percentage}%`);
        
        // Call onApply callback if provided
        if (onApply) {
          onApply({ code: validatedCode, percentage: result.percentage });
        }
      } else {
        // Default: mock validation for demo
        // In production, ALWAYS validate with backend
        const mockDiscounts = {
          'SAVE10': 10,
          'SAVE20': 20,
          'WELCOME': 15
        };
        
        if (!mockDiscounts[validatedCode]) {
          throw new Error('M√£ gi·∫£m gi√° kh√¥ng t·ªìn t·∫°i');
        }
        
        applyDiscount(validatedCode, mockDiscounts[validatedCode]);
        setSuccess(`ƒê√£ √°p d·ª•ng m√£ gi·∫£m gi√° ${mockDiscounts[validatedCode]}%`);
      }
      
      setCode('');
    } catch (err) {
      console.error('Discount code error:', err);
      setError(err.message || 'Kh√¥ng th·ªÉ √°p d·ª•ng m√£ gi·∫£m gi√°');
    } finally {
      setLoading(false);
    }
  };
  
  /**
   * Handle remove discount
   */
  const handleRemove = () => {
    removeDiscount();
    setCode('');
    setSuccess('');
    setError('');
  };
  
  /**
   * Handle input change with sanitization
   */
  const handleChange = (e) => {
    try {
      const sanitized = XSSProtection.stripHTML(e.target.value);
      setCode(sanitized);
      setError('');
    } catch (err) {
      setError('K√Ω t·ª± kh√¥ng h·ª£p l·ªá');
    }
  };
  
  return (
    <div className="mb-3">
      {discount > 0 ? (
        <Alert variant="success" className="d-flex justify-content-between align-items-center">
          <span>M√£ gi·∫£m gi√° ƒë√£ √°p d·ª•ng: <strong>{discount}%</strong></span>
          <Button 
            variant="outline-danger" 
            size="sm"
            onClick={handleRemove}
          >
            X√≥a
          </Button>
        </Alert>
      ) : (
        <>
          <Form.Label>M√£ gi·∫£m gi√°</Form.Label>
          <InputGroup>
            <Form.Control
              type="text"
              placeholder="Nh·∫≠p m√£ gi·∫£m gi√°"
              value={code}
              onChange={handleChange}
              disabled={loading}
              maxLength={20}
              autoComplete="off"
            />
            <Button 
              variant="primary"
              onClick={handleApply}
              disabled={loading || !code.trim()}
            >
              {loading ? 'ƒêang ki·ªÉm tra...' : '√Åp d·ª•ng'}
            </Button>
          </InputGroup>
          
          {error && (
            <Form.Text className="text-danger d-block mt-2">
              {error}
            </Form.Text>
          )}
          
          {success && (
            <Form.Text className="text-success d-block mt-2">
              {success}
            </Form.Text>
          )}
          
          <Form.Text className="text-muted d-block mt-1">
            V√≠ d·ª•: SAVE10, SAVE20, WELCOME
          </Form.Text>
        </>
      )}
    </div>
  );
};

export default DiscountCodeInput;